* {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: white;
  overflow-x: hidden;
}
.nav_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 80px;
  /* background-color: #e3e6e6; */
  color: black;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90vw;
}
.nav_heading {
  user-select: none;
  font-size: 32px;
  font-weight: bold;
  color: black;
  text-decoration: none;
}
.navigation_menu ul {
  margin-left: 40px;
  display: flex;
  gap: 20px;
}
.navigation_menu {
  display: flex;
  align-items: center;
}
.navigation_menu ul li {
  cursor: pointer;
  font-size: 20px;
}
.nav_button button {
  background: none;
  outline: none;
  border: none;
  color: black;
  user-select: none;
  cursor: pointer;
  font-size: 20px;
  border: 1px solid black;
  padding: 5px 15px;
  border-radius: 8px;
}
.container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 10px auto;
  width: 80vw;
}
.coin_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 15px;
}
.outer_coin_container:hover {
  transform: scale(105%);
  z-index: 100;
}
.coin_image {
  width: 100%;
  height: 250px;
  object-fit: contain;
  border: none;
  border-radius: 8px;
  background: none;
}
.outer_home_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.input_container{
  width: 50%;
  display: flex;
  align-items: center;
}
.search_input{
  margin: 15px 0;
  width: 100%;
  height: 45px;
  padding: 0 10px;
  border: none;
  outline: none;
  border-bottom: 1px solid black;
  font-size: 16px;
}
.input_button{
  margin: 0 -25px;
  font-size: 20px;
  background: none;
  cursor: pointer;
  outline: none;
  border: none;
}
.outer_coin_container{
  width: 30%;
  text-decoration: none;
  color: black;
  transition: transform ease 0.5s;
  border-radius: 8px;
  padding: 10px;
  margin: 10px;
  box-shadow: 5px 5px 5px gray;
  cursor: pointer;
  border: 1px solid gray;
}
.show_container{
  margin: 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.coin_details{
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid gray;
  padding-bottom: 24px;
}
.back_button{
  margin-left: 99px;
  font-size: 18px;
  color: gray;
  text-decoration: none;
}